import React from 'react'
import {firestoreConnect} from 'react-redux-firebase'
import {compose} from 'redux'
import {connect} from 'react-redux'
import { Button, Card, Table } from 'react-bootstrap'

const GameOver = props => {
    const {username, firebase, users, history} = props

    const sortWithScore = () => {
        return [...users].sort((a, b) => {
            return parseInt(b.score) - parseInt(a.score)
        });
    }
    
    const Restart = e => {
        e.preventDefault()

        history.push('/')
    }

    if(users&&username){
        let index = sortWithScore().findIndex(u=>u.id === firebase._.authUid)
        return (
            <Card>
                <Card.Header>
                <div>
                <h1>{username.slice(0, username.length-10)}</h1>
                Game Over
                <h5>You scored {sortWithScore()[index].tryscore}</h5>
                </div>
                <Button onClick={Restart}>Again</Button>
                </Card.Header>
                <Card.Body>
                    <Table striped bordered hover>
                        <thead>
                            <tr>
                                <th>â„–</th>
                                <th>Name</th>
                                <th>Record</th>
                            </tr>
                        </thead>

                        <tbody>
                            
                            {sortWithScore().map((u, index)=>(
                                <tr>
                                    <td>{index + 1}</td>
                                    <td>{u.email.slice(0, u.email.length-10)}</td>
                                    <td>{u.score}</td>
                                </tr>
                            ))}
                        </tbody>
                    </Table>
                </Card.Body>
            </Card>
        )
    }

    return <></>;
}

export default compose(
    firestoreConnect([{collection : 'users'}]),
    connect((state, props)=>({
        username : state.firebase.profile.email,
        users : state.firestore.ordered.users
    }))
)(GameOver)
