import React, {useState} from 'react'
import {Card, Button, Spinner, Form} from 'react-bootstrap'
import { connect } from 'react-redux'
import { firestoreConnect } from 'react-redux-firebase'
import { compose } from 'redux'

const Voc = props => {

    const {client, username, firebase, history, firestore, users} = props
    const [qnum, setQnum] = useState(Math.floor(Math.random()*10))
    const [ans, setAns] = useState('')
    const [scoree, setScoree] = useState(0)
    const [life, setLife] = useState(3)
    

    // const onLogout = e => {
    //     e.preventDefault()

    //     firebase.logout().then(()=>history.push('/'))
    // }
console.log(qnum)

    if(client&&username){
        let index = users.findIndex(u=>u.id === firebase._.authUid)

        const onAns = e => {
            e.preventDefault()
    
            if(ans.toLocaleLowerCase() === client[qnum].answer){
                setQnum(Math.floor(Math.random()*client.length))
                setScoree(scoree+1)
                setAns('')
            }
            if(ans !== '' && ans.toLocaleLowerCase() !== client[qnum].answer){
                if(life !== 1){
                    setQnum(Math.floor(Math.random()*client.length))
                    setLife(life-1)
                    setAns('')
                }
                if(life === 1){
                    
                    if(users[index].score < scoree){
                        const updateClient = {
                            email : username,
                            score : scoree,
                            tryscore : scoree
                        }
                    firestore.update({collection : 'users', doc : users[index].id}, updateClient)
                    history.push('/gameover')
                    }else{
                        const updateClient = {
                            email : username,
                            tryscore : scoree
                        }
                    firestore.update({collection : 'users', doc : users[index].id}, updateClient)
                    history.push('/gameover')
                    }
                }
            }
        }
        
        return (
            <>
                
                <div style={{height : "80vh"}} className='d-flex flex-column justify-content-center align-items-center'>
                <Card>
                    <Card.Header>
                    <h1>{username.slice(0, username.length-10)}</h1>
                    <p>Life : 
                        {[...Array(life)].map(p => {
                            return <i className="fa fa-heart mx-1"></i>
                        })}
                    </p>
                    <small>Score : {scoree}</small>
                    </Card.Header>

                    <Card.Body>
                        <Form onSubmit={onAns}>
                            <Form.Group>
                                <Form.Label>Text to translate:</Form.Label>
                                <h4>{client[qnum].question}</h4>
                                <Form.Control
                                type="text"
                                placeholder="Enter your answer"
                                value={ans}
                                onChange={e=>setAns(e.target.value)} />
                                <Button type="submit" className="my-3">Submit</Button>
                            </Form.Group>
                        </Form>
                    </Card.Body>
                </Card>


                {/* <Button onClick={onLogout}>
                    Log Out
                </Button> */}
                </div>
            
            </>
        )
    } else {
        return (
            <div style={{height : "80vh"}} className='d-flex flex-column justify-content-center align-items-center'>
            <Spinner animation="border" />
            </div>
        )
    }
}

export default compose(
    firestoreConnect([{collection : 'client'}]),
    firestoreConnect([{collection : 'users'}]),
    connect((state, props)=>({
        client : state.firestore.ordered.client,
        username : state.firebase.profile.email,
        users : state.firestore.ordered.users
    }))
)(Voc)
